import React from "react";

export default function ExecutandoWorkflow() {
  return (
    <div className="page-container">
      <h1>Executando um Workflow</h1>
      <p>
        O ONS ModelOps permite a execuo de workflows completos, integrando diferentes etapas do ciclo de vida dos modelos. O fluxo tpico envolve:
      </p>
      <ul>
        <li>Usurio acessa o Frontend</li>
        <li>Frontend envia requisies para a API ModelOps</li>
        <li>API ModelOps orquestra a execuo dos modelos</li>
        <li>Execuo distribuda no Cluster Efmero</li>
        <li>Resultados e metadados so armazenados e publicados</li>
      </ul>
      <img src="/img/ons-modelops-execucao.png" alt="Execuo Workflow" style={{maxWidth: 700}} />
    </div>
  );
}